:root {
  --blue: #00acdc;
}

html {
  box-sizing: border-box;
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo {
  margin: 0 0 0 50px;
}

.nav {
  align-items: center;
  margin-right: 50px;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  padding: 0;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background: var(--blue);
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
